:root {
  --timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  --animation-duration: 250ms;
}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen-Sans, Ubuntu, Cantarell,
    'Helvetica Neue', sans-serif;
  background-color: #fff;
  color: #212121;
}

.gallery {
  display: flex;
  width: 1200px;
  flex-wrap: wrap;
  padding: 0;
  list-style: none;
  margin-left: auto;
  margin-right: auto;
}

.gallery__item {
  position: relative;
  width: 380px;
  height: 240px;
  margin: 6px;
  box-shadow: 1px 1px 3px 0px rgba(0, 0, 0, 0.2), 0px 1px 1px 0px rgba(0, 0, 0, 0.14),
    0px 2px 1px -1px rgba(0, 0, 0, 0.12);
}

.gallery__image:hover {
  transform: scale(1.05);
  box-shadow: 2px 4px 6px 0px rgba(0, 0, 0, 0.2), 0px 4px 5px 0px rgba(0, 0, 0, 0.14),
    1px 3px 1px -1px rgba(0, 0, 0, 0.12);
}

.gallery__image {
  height: 100%;
  width: 100%;
  object-fit: cover;
  transition: transform var(--animation-duration) var(--timing-function);
}

.gallery__link {
  display: block;
  text-decoration: none;
  height: 100%;
}

.gallery__link:hover {
  cursor: zoom-in;
}

/* Lightbox */
.lightbox {
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  opacity: 0;
  pointer-events: none;
  transition: opacity var(--animation-duration) var(--timing-function);
}

.lightbox.is-open {
  opacity: 1;
  pointer-events: initial;
}

.lightbox__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.8);
}

.lightbox__content {
  transform: scale(0.8);
  transition: transform var(--animation-duration) var(--timing-function);
}

.lightbox.is-open .lightbox__content {
  transform: scale(1);
}

.lightbox__image {
  display: block;
  width: auto;
  height: auto;
  max-height: 100vh;
  max-width: 100vw;
}

.lightbox__button {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 48px;
  height: 48px;
  padding: 0;
  margin: 0;
  border: none;
  border-radius: 50%;
  background-color: transparent;
  color: #fff;
  cursor: pointer;
  transition: background-color var(--animation-duration) var(--timing-function),
    opacity var(--animation-duration) var(--timing-function);
  outline: none;

  background-position: center;
  background-size: 60%;
  background-repeat: no-repeat;
}

.lightbox__button--close {
  top: 8px;
  right: 8px;
  background-image: url('../images/icon-close.svg');
}

.lightbox__button--close:hover,
.lightbox__button--close:focus {
  background-color: rgba(0, 0, 0, 0.5);
}

.lightbox__button--arrow-left {
  top: 50%;
  left: 8px;
  background-image: url('../images/arrow-left.svg');
  opacity: 0.4;
}

.lightbox__button--arrow-right {
  top: 50%;
  right: 8px;
  background-image: url('../images/arrow-right.svg');
  opacity: 0.4;
}

.lightbox__button--arrow-left:hover,
.lightbox__button--arrow-left:focus,
.lightbox__button--arrow-right:hover,
.lightbox__button--arrow-right:focus {
  opacity: 0.8;
}
